header {
  width: 100%;
  z-index: 1000;
  justify-content: center;
  padding: 20px 0;
  position: absolute;
  top: 0;
  left: 0;
}

.header-el {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  width: 98px;
  height: 15px;
}

.header-color {
  background-color: var(--background);
  box-shadow: 0px 0px 1px var(--text05);
}

.header-icon {
  width: 16px;
  height: 16px;
  fill: var(--text);
  position: absolute;
  top: 50%;
  left: 18px;
  transform: translateY(20%);
}

.header-btn {
  background: none;
  border-color: rgba(0, 0, 0, 0);
  outline: none;
  position: absolute;
}

.header-input {
  color: var(--text);
  border: 1px solid var(--text03);
  width: 108px;
  background: none;
  border-radius: 1000px;
  padding: 7px 18px 7px 42px;
