  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header-input::-ms-input-placeholder {
  color: var(--text);
  font-weight: bold;
}

.header-input::placeholder {
  color: var(--text);
  font-weight: bold;
}

.header-find {
  gap: 40px 16px;
  display: flex;
}

.gallery__link {
  border: none;
  gap: 16px;
  transition: all .3s;
}

.gallery__link:hover, .gallery__link:focus {
